* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.home-container {
  padding: 15px 5px;
  /* display: flex;
  justify-content: center;
  align-items: start;
  gap: 10px; */
  /* background: #fff; */
  margin-top: 70px;
  height: calc(100vh - 100px);
  margin-right: 35px;
  /* position: relative; */
}
.toggleBtn .actives {
  background: #035771 !important;
  color: #fff !important;
}
.toggleBtn .notactives {
  background: #fff !important;
  color: #035771 !important;
}

.toggleBtn button:first-child {
  width: 50%;
  border: 1px solid #035771;
  border-bottom-right-radius: 1px !important;
  border-top-right-radius: 1px !important;
  cursor: pointer;
  box-shadow: 5px 6px 17px 0 rgba(0, 0, 0, 0.11);
  border-radius: 8px;
}
#content {
  padding: 0 20px 0;
}
.toggleBtn button:last-child {
  width: 50%;
  border: 1px solid #03577170;
  border-bottom-left-radius: 1px !important;
  border-top-left-radius: 1px !important;
  cursor: pointer;
  box-shadow: 5px 6px 17px 0 rgba(0, 0, 0, 0.11);
  border-radius: 8px;
}
.toggleMobile {
  display: none;
}
.toggleBtn button {
  border-radius: 8px !important;
}
.refreshBtn {
  position: absolute;
  right: 5px;
  bottom: 25px;
  z-index: 999;
  cursor: pointer;
}
@media screen and (max-width: 767px){
  .home-container {
    display: block;
    margin-top: 15px;
    height: inherit;
    padding: 0;
  }
  .toggleMobile {
    display: block;
  }
  .hideme{
    display: none;
  }

.qicon {
  /* position: absolute; */
  bottom: 0;
  right: 12px;
  cursor: pointer;
}

.homeTxt{
  color: #035771 !important;
  
}
}
.home-container .homeTxt {
  font-weight: bold;
  color: #0f617a;
}
